<template>
	<div class="cate-item-hd">
		<div @click="back()"></div>
		<input type="search" placeholder="由你购,最想购...">
	</div>
</template>


<style>
	.cate-item-hd{
		width: 100%;
		position: absolute;
		top: 0;
		height: 1.066667rem;
		border-bottom: 1px solid #f0f0f0; 
	}
	.cate-item-hd div{
		display: inline-block;
		width: 0.8rem;
		height: 0.8rem;
		background: url(../../../../assets/fonts/back-icon.png) no-repeat center;
		background-size: contain;
		margin: 0.133333rem 0 0 0.133333rem;
		vertical-align: middle;
	}
	.cate-item-hd input{
		width: 80%;
		height: 0.746667rem;
		padding-left: 0.8rem;
		font-size: 0.373333rem;
		vertical-align: middle;
		border: none;
		outline: none;
		margin: 0.133333rem 0 0 0.266667rem;
		border-radius: 0.133333rem;
		background: url(../../../../assets/fonts/search.png) #eee no-repeat;
		background-size: 0.533333rem;
		background-position: 0.133333rem 0.106667rem;
	}

</style>


<script>
	export default{
		methods: {
			back(){
				window.history.back();
			}
		}
	}
</script>


