<template>
	<div id="back-hd">
		<span @click="back"></span>
	</div>
</template>



<style>
	#back-hd{
		width: 100%;
		position: absolute;
		top: 0;
		height: 1.066667rem;
		border-bottom: 1px solid #f0f0f0; 
	}
	#back-hd span{
		display: inline-block;
		width: 0.8rem;
		height: 0.8rem;
		background: url(../../../../assets/fonts/back-icon.png) no-repeat center;
		background-size: contain;
		margin: 0.133333rem;
	}
</style>



<script>
	export default{
		methods: {
			back(){
				window.history.back();
			}
		}
	}
</script>


